.App {
  width: 580px;
  height: 600px;
  overflow: hidden;
}

.AppIFrame {
  width: 600px;
  height: 600px;
  background-color: transparent;
}

.AppLoader {
  width: 600px;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

body {
  user-select: none;
  font-family: Roboto, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.85);
  background-color: #ffffff;
  --border-gray: rgb(201, 204, 209);
  --section-margin: 16px;
  --section-padding: 24px;
  --section-padding-top-bottom: 6px;
}

a:visited{
  color:blue;
}

/* Common components */
.root-container {
  width: 100%;
}

.section-container {
  width: calc(100% - var(--section-padding) * 2);
  display: block;
  overflow: hidden;
  margin: 0;
}

.footer-section {
  border-top: 1px solid var(--border-gray);
  bottom: 0;
  position: absolute;
}

.normal-section {
  padding: var(--section-padding-top-bottom) var(--section-padding);
}

.section-vertical-centered {
  align-items: center;
  display: flex;
  padding: var(--section-padding-top-bottom) var(--section-padding);
}

.title-container {
  display: flex;
  flex-direction: row;
  height: 44px;
}

.title {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.title-row {
  display: flex;
  flex-direction: row;
}

.title-icon {
  margin-top: 6px;
  width: 28px;
  height: 28px;
}

.title-subtext-section {
  font-family: Roboto, Arial, sans-serif;
  display: flex;
  flex-direction: row;
}

.title-subtext-count {
  margin-left: 8px;
  width: 18px;
  height: 18px;
  border-radius: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: rgba(57,115,0,1);
  background-color: rgba(218,242,194,1);
}

.title-subtext {
  margin: 2px 0 0 4px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.65);
}

.title-text, .text-title {
  color: rgb(0, 0, 0);
  font-size: 20px;
  font-family: Roboto-bold;
  font-weight: bold;
  margin-top: 8px;
  margin-left: 8px;
}

.text-body {
  color: rgb(0, 0, 0);
  font-size: 14px;
  font-weight: 400;
}

.text-label {
  color: rgb(0, 0, 0);
  font-family: Roboto-bold;
  font-size: 14px;
  font-weight: 700;
  margin: 0;
}

.text-supporting {
  display: inline;
  margin: 0;
  font-size: 12px;
}

.close-button {
  margin-top: 6px;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.root-button {
  width: 20px;
  height: 20px;
  margin-top: 8px;
  margin-right: 8px;
}

.root-icon {
  width: 20px;
  height: 20px;
  margin-top: 8px;
}

.control-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.control-container-left{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.margin-right-10 {
  margin-right: 10px;
}

.button {
  cursor: pointer;
  max-width: 100%;
  text-align: center;
  height: 36px;
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 12px;
  padding-right: 12px;
  border-radius: 6px;
  border-width: 0px;
  font-family: Roboto, Arial, sans-serif;
  letter-spacing: normal;
  font-size: 14px;
  line-height: 36px;
  text-align: center;
}

@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.spinner {
    display: block;
    will-change: transform;
    height: 16px;
    width: 16px;
    animation-duration: 550ms;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-fill-mode: none;
    animation-play-state: running;
    animation: spin 550ms linear infinite;
}

.button:focus {
  outline: none;
  box-shadow: 0;
  resize: none;
}

.button-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  padding-right: 4px;
}

.button-icon {
  margin-top: 10px;
  width: 16px;
  height: 16px;
}

.button-text {
  margin-left: 4px;
}

.primary-button {
  margin-left: 12px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  background-color: rgb(24, 119, 242);
  border-color: rgb(24, 119, 242);
}

.disabled, .disabled:hover, button:disabled, button[disabled] {
  cursor: default;
  background-color:rgb(176, 213, 255) !important;
  border-bottom-color:rgb(176, 213, 255) !important;
}

.primary-button:hover {
  background-color: rgb(4, 59, 135);
  border-color: rgb(4, 59, 135);
}

.flat-button {
  color: rgb(28, 30, 33);
  font-weight: normal;
  border: none;
  background-color: white;
}

.flat-button:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.default-button {
  color: rgb(28, 30, 33);
  font-weight: normal;
  background-color: rgba(0, 0, 0, 0.05);
  border-color: rgba(0, 0, 0, 0.05);
}

.default-button:hover {
  background-color: rgba(0, 0, 0, 0.15);
  border-color: rgba(0, 0, 0, 0.15);
}

.topic-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

/* Main topic creation page */
.topic-text-label {
  font-family: Roboto-bold, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  font-weight: bold;
}

.topic-text-input {
  margin-top: 5px;
  border-radius: 5px;
  resize: none;
  border: 1px solid #c9ccd1;
  box-sizing: border-box;
  padding: 8px;
  width: 100%;
  min-height: 80px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing:normal;
  overflow: auto;
  outline: none;
  box-shadow: none;
  resize: none;
}

.topic-text-input:focus {
  border: 1px solid rgb(24, 119, 242);
}

.shared-topic-warning-container {
  margin-top: 8px;
  background-color: #E6F2FF;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  padding: 16px;
}

.shared-topic-label {
  margin-top: 2px;
  font-size: 14px;
  font-family: Roboto-bold, Roboto, Arial, sans-serif;
  color: #1C1E21;
  font-weight: bold;
  margin-left: 12px;
}

.shared-topic-message {
  margin-top: 2px;
  font-size: 14px;
  letter-spacing:normal;
  font-weight: 400;
  font-family: Roboto, Arial, sans-serif;
  color: #1C1E21;
  margin-left: 4px;
}

.shared-topic-icon {
  height: 20px;
  width: 20px;
}

.button:hover {
  outline: none;
  box-shadow: none;
  resize: none;
}

.redirect-button {
  background-color: #4CAF50; /* Green */
  border: 1px solid green;
  border-radius: 5px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.full-width {
  width: 100%;
}

.conversation-guest-container {
  max-height: 260px;
  height: auto;
  overflow-y: auto;
  width: 100%;
}

.collapsible-section-container {
  padding-bottom: 12px;
}

.collapsible-section-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 8px;
  margin: 0 var(--section-padding);
  border-bottom: 1px solid var(--border-gray);
}

.collapsible-section-count {
  border-radius: 18px;
  height: 18px;
  width: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: rgba(57,115,0,1);
  background-color: rgba(218,242,194,1);
}

.collapsible-section-title {
  margin: 4px 4px 0 0;
  font-weight: 700;
  font-size: 14px;
}

.collapsible-section-selector {
  padding-left: var(--section-margin);
  width: calc(100% - var(--section-margin));
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.collapsible-hide {
  display: none;
}

.collapsible-show {
  display: flex;
}

.collapsible-section-header-left {
  margin-top: 6px;
  display: flex;
  flex-direction: row;
}

.collapsible-section-header-right {
  display: flex;
  flex-direction: row;
}

.collapsible-section-collapse-button {
  cursor: pointer;
  border-radius: 4px;
  padding: 6px;
  background-color: rgba(0, 0, 0, 0.05);
  margin-left: 12px;
}

.collapsible-button-image {
  height: 16px;
  width: 16px;
}

.collapsible-section-empty {
  width: calc(100% - var(--section-margin) * 2);
  margin: 10px 6px;
  border-radius: 8px;
  background-color: rgba(0,0,0,.05);
  padding: 10px 0;
  display: flex;
  justify-content: center;
  color: rgba(0, 0, 0, 0.85);
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.direct-report-cell {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  width: 123px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid var(--border-gray);
  background: #fff;
  margin: 8px 6px 0 6px;
  width: 122px;
  cursor: pointer;
}

.direct-report-select-all {
  cursor: pointer;
  padding-left: 6px;
  margin-bottom: 8px;
}

.direct-report-cell:hover {
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.1);
}

.direct-report-selected {
  background: rgb(231, 243, 255);
  border: 1px solid rgb(24, 119, 242);
}

.direct-report-cell-text {
  margin-top: 10px;
  margin-left: 4px;
  color: rgba(0, 0, 0, 0.85);
  direction: ltr;
  display: block;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing:normal;
  line-height:20.0001px;
  text-align:left;
  width: 75px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.direct-report-cell-image {
  margin-top: 6px;
  margin-left: 9px;
}

.profile-photo-image {
  border-radius: 100%;
  background-position: center center;
  background-size: cover;
  height: 100%;
  width: 100%;
}

.profile-photo {
  border: 2px solid #fff;
  box-shadow: 0 0 1px #bec3c9;
  display: block;
  height: 26px;
  border-radius: 100%;
  width: 26px;
}

.direct-report-confirmation-container {
  justify-content: space-between;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.direct-report-link-cell {
  margin-top: 8px;
  min-width: 261px;
  height: 48px;
  display: flex;
  flex-direction: row;
  border-radius: 8px;
  border: 1px solid var(--border-gray);
  background: #fff;
}

.direct-report-link-error-cell{
  margin-top: 8px;
  min-width: 261px;
  height: 48px;
  display: flex;
  flex-direction: row;
  border-radius: 8px;
  border: 1px solid rgb(240, 40, 73);
  background: rgba(255, 0, 0, 0.05);
}

.direct-report-link-cell-text-container {
  flex-grow: 1;
  margin-left: 4px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.direct-report-link-cell-text {
  color: rgba(0, 0, 0, 0.85);
  direction: ltr;
  display: block;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing:normal;
  line-height:20.0001px;
  text-align:left;
  width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.direct-report-link-cell-error-text {
  color: rgb(240, 40, 73);
  direction: ltr;
  display: block;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  letter-spacing:normal;
  line-height:20.0001px;
  text-align:left;
  width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.direct-report-link-cell-image {
  margin-top: 10px;
  margin-left: 9px;
}

.direct-report-link-button-icon {
  margin-top: 4px;
  width: 16px;
  height: 16px;
}

.direct-report-link-button {
  width: 32px;
  height: 32px;
  margin-top: 7px;
  margin-right: 7px;
  border-radius: 4px;
  border: 0;
  cursor: pointer;
}

.direct-report-link-button:hover, .direct-report-link-button:focus {
  overflow: auto;
  outline: none;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  resize: none;
}

.checkbox-label-container {
  display: flex;
  flex-direction: row;
  margin-top: 4px;
}

.checkbox-container {
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0.15);
  height: 24px;
  width: 24px;
  border-radius: 6px;
  align-items: center;
  background-color: #fff;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  pointer-events: none;
  transition-duration: .1s;
  transition-property: border, background-color, box-shadow;
  transition-timing-function: ease;
  cursor: pointer;
}

.checkbox-container:focus {
  background: rgb(231, 243, 255);
  border: 1px solid #1877f2;
  overflow: auto;
  outline: none;
  box-shadow: 0;
  resize: none;
}

.checkbox-image-placeholder {
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  height: 16px;
  width: 16px;
}

.checkbox-checkmark {
  height: 16px;
  width: 16px;
  transition: transform .1s ease;
  opacity: 0;
}

.checkbox-selected {
  opacity: 1;
  transition: transform .1s ease;
}

.checkbox-label-text {
  margin-top: 4px;
  margin-left: 8px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.75);
}

.nux-container {
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nux-image {
  width: 240px;
  height: 130px;
}

.nux-text-container {
  width: 464px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nux-title {
  font-size: 20px;
  font-family: Roboto-bold, Roboto, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.75);
  font-weight: bold;
  margin-top: 24px;
}

.nux-description {
  font-family: Roboto, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  text-align: center;
  margin-top: 8px;
}

.actions {
  margin-top: 40px;
  display: flex;
  align-items: start;
}

.secondary-actions {
  margin-top: 12px;
}

.nux-button {
  margin-top: 8px;
}

.opted-out-success-container {
  margin-top: 8px;
  background-color: #DAF2C2;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  padding: 16px;
}

.opted-out-success-label {
  margin-top: 2px;
  font-size: 14px;
  font-family: Roboto-bold, Roboto, Arial, sans-serif;
  color: #1C1E21;
  font-weight: bold;
  margin-left: 12px;
}

.opted-out-success-icon {
  height: 20px;
  width: 20px;
}

.opted-out-container {
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.opted-out-description {
  font-family: Roboto, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  text-align: center;
  margin-top: 8px;
}

.confirmation-overlay {
  position: absolute;
  background-color: white;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.tab-container {
  display: flex;
  padding: var(--section-padding-top-bottom) var(--section-padding);
}

.tab-item {
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  margin-right: 2px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.tab-item p {
  margin: 0;
}

.tab-item.selected {
  background-color: #EDF5FF;
  color: #1461CC;
  font-weight: bold;
}

.pill-component {
  align-items: center;
  background-color: #2A71E4;
  border-radius: 100px;
  color: #FFFFFF;
  display: flex;
  font-family: 'Roboto';
  font-weight: bold;
  font-size: 11px;
  line-height: 20px;
  margin: 0 4px;
  padding: 0 8px;
}

.styled-select {
  appearance: none;
  background-image: url("down.png");
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: calc(100% - 8px);
  background-size: 16px;
  background-color: rgb(228, 230, 235);
  border-radius: 6px;
  border: none;
  color: rgb(5, 5, 5);
  font-size: 14px;
  height: 36px;
  line-height: 20px;
  margin-top: 8px;
  min-width: 200px;
  outline: none;
  padding: 8px 32px 8px 12px;
  text-align: left;
}

.styled-select:hover {
  background-color: rgb(216, 218, 222);
}

.toast-close-button {
  border: 0;
  background-color: transparent;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  margin-right: 10px;
  margin-left: auto;
}

.toast-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 70px;
  left: 0px;
  width: 100%;
}

.toast-icon {
  height: 16px;
  width: 16px;
  margin: 0 8px;
}

.toast {
  padding: 4px 0;
  border-radius: 8px;
  display: flex;
  align-items: center;
  height: 36px;
  width: 300px;
  font-size: 14px;
}

.toast.success {
  background-color: rgba(49, 162, 76, 0.2);
}

.toast.failure{
  background-color: rgba(240, 40, 73, 0.2);
}
